function renderPopovers(icon) {
  let component = null;
  if (icon.hasDropdown && icon.isEnabled == true) {
    switch (icon.menuId) {
      case "cart":
        component = HeaderIconsDropdownCart;
        break;
      case "wishList":
        component = HeaderIconsDropdown;
        break;
      case "account":
        component = HeaderIconsDropdownMenu;
        break;
    }
