<template>
  <nav id="nav">
    <router-link to="#" class="nav-opener" @click="toggleNav">
      <img src="@/esf_thehague_schinsleder/assets/svg/bars.svg" class="bars" width="22" alt="bars" />
    </router-link>
    <transition name="slide-fade">
      <div class="nav-collapse" :class="{ show: navExpanded }">
        <NavbarContent @clicked="toggleNav" />
      </div>
    </transition>
  </nav>
</template>

<script setup>
import { computed, onMounted, onUnmounted, watch } from "vue";
import { useStore } from "vuex";
import NavbarContent from "@/esf_thehague_schinsleder/core/components/NavbarContent.vue";

// Setup the store access
const store = useStore();

// Computed property using Composition API
const navExpanded = computed({
  get() {
    return store.getters["menu/getNavbar"];
  },
  set() {
    // Nothing to do
  },
});

watch(navExpanded, async (newValue, oldValue) => {

  if (newValue) {
    document.body.classList.add("overflow-hidden");
  }
  else {
    document.body.classList.remove("overflow-hidden");
  }
})

const toggleNav = () => {
  store.dispatch("menu/toggleNavbar");
};

const resize = () => {
  document.body.classList.add("resize-active");
  if (window.innerWidth > 991) {
    navExpanded.value = false;
  }
  clearTimeout(resizeTimer);
  resizeTimer();
};

const resizeTimer = () => {
  setTimeout(() => {
    document.body.classList.remove("resize-active");
  }, 400);
};

onMounted(() => {
  window.addEventListener("resize", resize);
});

onUnmounted(() => {
  window.removeEventListener("resize", resize);
});
</script>
