<template>
    <div id="wrapper" :class="deviceClass" >
        <div class="col-xl-2 d-none d-xl-block sidebar" v-if="isClient">
            <Sidebar />
        </div>
        <div class="col-12 col-xl-10 ">
            <Header :class="showTopbar" />
            <main id="main" :class="wrapperClass">
                <slot></slot>
            </main>
            <Footer />
        </div>

        <CookieBarWrap />
    </div>
</template>
<script setup>
import { ref, onMounted } from 'vue'
import Header from "@/esf_thehague_schinsleder/core/components/common/Header.vue";
import Footer from "@/esf_thehague_schinsleder/core/components/common/Footer.vue";
import Sidebar from "@/esf_thehague_schinsleder/core/components/common/Sidebar.vue";
import CookieBarWrap from "@/esf_thehague_schinsleder/core/components/CookieBar";
// import ClientOnly from "vue-client-only";
const isClient = ref(false);

onMounted(() => {
    isClient.value = true;
});


const props = defineProps({
    wrapperClass: {
        type: String,
        required: false,
    },
});

const deviceClass = ref('desktop');
const showTopbar = ref();

const resizeWindow = () => {
    const windowWidth = window.innerWidth;
    deviceClass.value = windowWidth >= 1025 ? 'desktop d-flex container' : 'mobile container-fluid  px-0';
};

onMounted(() => {
    resizeWindow();
    window.addEventListener('resize', resizeWindow);
    const mainElement = document.querySelector("main");
    if (mainElement.classList.contains("home")) {
        showTopbar.value = "show-topbar";
    }
});

</script>